<template>
  <div>
    <div class="demo-cell">
      <h-cell-group>
        <h-cell title=""></h-cell>
        <h-cell title="" value=""></h-cell>
      </h-cell-group>
    </div>
    <div class="demo-cell">
      <h-cell-group>
        <h-cell title="" value="" label=""></h-cell>
      </h-cell-group>
    </div>
    <div class="demo-cell">
      <h-cell-group>
        <h-cell value="" label=""></h-cell>
      </h-cell-group>
    </div>
    <div class="demo-cell">
      <h-cell-group>
        <h-cell icon="wechat" value="" label=""></h-cell>
      </h-cell-group>
    </div>
    <div class="demo-cell">
      <h-cell-group>
        <h-cell title="" value="" isLink></h-cell>
        <h-cell title="" value="" isLink arrow-direction="down" @click="handleClick"></h-cell>
      </h-cell-group>
    </div>
  </div>
</template>

<script>

export default {
  methods: {
    handleClick() {
      this.$dialog.alert({
        message: 'hello'
      })
    }
  }
}
</script>

<style lang="less">
  .demo-cell {
    margin-top: 20px;
  }
</style>



