<template>
  <div>
    <div class="field-demo">
      <h-cell-group>
        <h-field ref="field" v-model="value" :placeholder="''" clearable></h-field>
      </h-cell-group>
    </div>
    <div class="field-demo">
      <h-cell-group>
        <h-field v-model="value2" label="" :placeholder="''" clearable required></h-field>
        <h-field v-model="value3" type="password" label="" :placeholder="''" maxlength="6" autocomplete="off" clearable required></h-field>
      </h-cell-group>
    </div>
    <div class="field-demo">
      <h-cell-group>
        <h-field v-model="value4" :label="label" :placeholder="''" clearable center>
          <h-button slot="button" size="small" type="primary"></h-button>
        </h-field>
      </h-cell-group>
    </div>
    <div class="field-demo">
      <h-cell-group>
        <h-field v-model="value5" :label="label" :placeholder="''" errorMessage=""></h-field>
      </h-cell-group>
    </div>
    <div class="field-demo">
      <h-cell-group>
        <h-field v-model="value6" :label="label" :placeholder="''" disabled></h-field>
      </h-cell-group>
    </div>
    <div class="field-demo">
      <h-cell-group>
        <h-field v-model="value7" :label="label" :placeholder="''" icon="question" :onIconClick="handleClick"></h-field>
        <h-field type="textarea" v-model="value8" label="textarea" :placeholder="''" autosize rows="1"></h-field>
      </h-cell-group>
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      value: '',
      value2: '',
      value3: '',
      value4: '',
      value5: '',
      value6: '',
      value7: '',
      value8: '',
      label: ''
    }
  },
  methods: {
    handleClick() {
      this.$toast({
        message: 'question'
      })
    }
  }
}
</script>

<style lang="less">
  .field-demo {
    margin: 20px 0;
  }
</style>



